import {ReactComponent as DownloadedSVG} from "./source/‰∏ãËΩΩ.svg";
import "./Right.css"

/**
 * ÈÖçÁΩÆÁîüÊàêÂô®ÁªÑ‰ª∂
 */
export default class ConfigGeneratorCard extends Component {
    state = {
        // ÈÖçÁΩÆÁîüÊàêÂô®ÂèØËßÅÊÄß
        configGeneratorVisible: false,
        // ÈÄâÊã©ÂèëË°åÁâà
        selectDistrib: undefined,
        // ÈÄâÊã©ÁöÑÂèëË°åÁâàÁöÑÁâàÊú¨Âè∑ÊàñÁâàÊú¨Âà´Âêç
        selectVersion: undefined,
        // ÊòØÂê¶ÊòæÁ§∫ËΩØ‰ª∂Ê∫êÈÖçÁΩÆÊñáÊú¨Âùó
        showConfigBlock: false,
        // ËΩØ‰ª∂Ê∫êÈÖçÁΩÆÊñáÊú¨Âùó
        configBlock: undefined
    };
    myStyle = {
        buttonStyle: {color: '#9C0C13', borderColor: '#9C0C13'},
        CascaderStyle: {outlineColor: '#9C0C13'}
    }

    /**
     * ÊòæÁ§∫ÈÖçÁΩÆÁîüÊàêÂô®ÂØπËØùÊ°Ü
     */
    showDownloadForm = () => {
        this.setState({configGeneratorVisible: true});
    };

    /**
     * ÈÄÄÂá∫ÈÖçÁΩÆÁîüÊàêÂô®ÂØπËØùÊ°Ü
     */
    handleConfigGeneratorCancel = () => {
        this.setState({configGeneratorVisible: false});
    };

    /**
     * Á°ÆËÆ§ÈÄâÊã©ÂèëË°åÁâàÂíåÁâàÊú¨
     *
     * @param selectedOptions Á∫ßËÅîÈÄâÊã©Âô®ÂêÑÁ∫ßÁöÑÂÄº„ÄÇ
     *        ÂÖ∂‰∏≠[0]‰∏∫ÂèëË°åÁâàÂêçÁß∞Ôºå[1]‰∏∫ÁâàÊú¨Âà´Âêç
     */
    onConfigChange = selectedOptions => {
        this.setState({
            selectDistrib: selectedOptions[0],
            selectVersion: selectedOptions[1]
        });
    };

    /**
     * ÁîüÊàêÈÖçÁΩÆ
     */
    handleGenerateConfig = () => {
        let configBlock;
        switch (this.state.selectDistrib) {
            case "ubuntu":
                configBlock = buildUbuntuBlock(this.state.selectVersion);
                this.setState({showConfigBlock: true, configBlock: configBlock});
                break;
            case "debian":
                configBlock = buildDebianBlock(this.state.selectVersion);
                this.setState({showConfigBlock: true, configBlock: configBlock});
                break;
            case "archlinux":
                configBlock = buildArchBlock();
                this.setState({showConfigBlock: true, configBlock: configBlock});
                break;
            case "rocky":
                configBlock = buildRockyBlock();
                this.setState({showConfigBlock: true, configBlock: configBlock});
                break;
            default:
                break;
        }
    };

    render() {
        return (
            <div>
                <h2>ÈÖçÁΩÆÁîüÊàê</h2>
                <p>ÁîüÊàêÂèëË°åÁâàÁöÑÈÖçÁΩÆÊñá‰ª∂</p>
                <Button
                    type="default"
                    icon={<Icon component={ConfigSVG}/>}
                    onClick={this.showDownloadForm}
                    style={this.myStyle.buttonStyle}
                >
                    ÈÖçÁΩÆÁîüÊàêÂô®
                </Button>
                <Modal
                    visible={this.state.configGeneratorVisible}
                    title={"ÈÖçÁΩÆÁîüÊàêÂô®"}
                    onCancel={this.handleConfigGeneratorCancel}
                    footer={null}
                    width={800}
                >
                    <Form layout="vertical">
                        <Form.Item>
                            <Row gutter={8}>
                                <Col span={20}>
                                    <Cascader
                                        expandTrigger="hover"
                                        placeholder="ËØ∑ÈÄâÊã©ÂèëË°åÁâà"
                                        options={this.props.config}
                                        onChange={this.onConfigChange}
                                        autoFocus={<style></style>}
                                    />
                                </Col>
                                <Col span={4}>
                                    <Button type="default"
                                            onClick={this.handleGenerateConfig}
                                            icon={<Icon component={ConfigSVG}/>}
                                            style={this.myStyle.buttonStyle}>
                                        ÁîüÊàê
                                    </Button>

                                </Col>
                            </Row>
                            <Row>
                                <ConfigBlock
                                    showConfigBlock={this.state.showConfigBlock}
                                    configBlock={this.state.configBlock}
                                />
                            </Row>
                        </Form.Item>
                    </Form>
                </Modal>
            </div>
        );
    }
}

/**
 * ÈÖçÁΩÆÁîüÊàêÂô®ÁöÑËΩØ‰ª∂Ê∫êÊñáÊú¨ÂùóÁªÑ‰ª∂
 */
class ConfigBlock extends Component {
    render() {
        let block = null;
        if (this.props.showConfigBlock) {
            // console.log(typeof this.props.configBlock);
            // block = this.props.configBlock.split("\n").map(function(item, id) {
            //   return (
            //     <span key={id}>
            //       {item}
            //       <br />
            //     </span>
            //   );
            // });
            block = this.props.configBlock;
            block = block.substring(0, block.length - 1);
        }
        if (block != null) {
            return (
                <Col className="config-block" span={24}>
                    <SyntaxHighlighter language="plaintext" style={dark}>
                        {block}
                    </SyntaxHighlighter>
                </Col>
            );
        } else {
            return (
                null
            );
        }
    }
}

/**
 * ÊûÑÂª∫UbuntuËΩØ‰ª∂Ê∫êÈÖçÁΩÆÁöÑ‰∏ÄË°å
 *
 * @param val ËΩØ‰ª∂ÂåÖÊ†ºÂºè
 * @param version ÁâàÊú¨Âà´Âêç
 * @returns {string} ËøîÂõûUbuntuÈÖçÁΩÆÁöÑ‰∏ÄË°å
 */
function buildUbuntuLine(val, version) {
    return (
        val +
        " https://mirrors.sdu.edu.cn/ubuntu/ " +
        version +
        " main restricted universe multiverse\n"
    );
}

/**
 * ÊûÑÂª∫UbuntuËΩØ‰ª∂Ê∫êÈÖçÁΩÆÁöÑÊñáÊú¨Âùó
 *
 * @param version ÁâàÊú¨Âà´Âêç
 * @returns {string} ËøîÂõûUbuntuËΩØ‰ª∂Ê∫êÈÖçÁΩÆÁöÑÊñáÊú¨Âùó
 */
function buildUbuntuBlock(version) {
    return (
        // "# ÈªòËÆ§Ê≥®Èáä‰∫ÜÊ∫êÁ†ÅÈïúÂÉè‰ª•ÊèêÈ´ò apt update ÈÄüÂ∫¶ÔºåÂ¶ÇÊúâÈúÄË¶ÅÂèØËá™Ë°åÂèñÊ∂àÊ≥®Èáä\n" +
        buildUbuntuLine("deb", version) +
        buildUbuntuLine("deb", version + "-security") +
        buildUbuntuLine("deb", version + "-updates")
    );
}

/**
 * ÊûÑÂª∫DebianËΩØ‰ª∂Ê∫êÈÖçÁΩÆÁöÑ‰∏ÄË°å
 *
 * @param val ËΩØ‰ª∂ÂåÖÊ†ºÂºè
 * @param version ÁâàÊú¨Âà´Âêç
 * @returns {string} ËøîÂõûDebianÈÖçÁΩÆÁöÑ‰∏ÄË°å
 */
function buildDebianLine(val, version) {
    return (
        val +
        " https://mirrors.sdu.edu.cn/debian" +
        version +
        " main contrib non-free\n"

    );
}

/**
 * ÊûÑÂª∫DebianËΩØ‰ª∂Ê∫êÈÖçÁΩÆÁöÑÊñáÊú¨Âùó
 *
 * @param version ÁâàÊú¨Âà´Âêç
 * @returns {string} ËøîÂõûDebianËΩØ‰ª∂Ê∫êÈÖçÁΩÆÁöÑÊñáÊú¨Âùó
 */
function buildDebianBlock(version) {
    return (
        // "ÁõÆÂâçËøòÊú™Êèê‰æõdebian-securityÔºåËØ∑Ê≥®ÊÑèÊ∑ªÂä†\n" +
        buildDebianLine("deb", "") +
        buildDebianLine("deb", "" + "-security buster/updates") +
        buildDebianLine("deb", " " + "buster-updates")
    );
}

/**
 * ÊûÑÂª∫ArchLinuxËΩØ‰ª∂Ê∫êÈÖçÁΩÆÁöÑÊñáÊú¨Âùó
 *
 * @returns {string} ËøîÂõûArchLinuxËΩØ‰ª∂Ê∫êÈÖçÁΩÆÁöÑÊñáÊú¨Âùó
 */
function buildArchBlock() {
    return "Server = https://mirrors.sdu.edu.cn/archlinux/$repo/os/$arch ";
}

/**
 * ÊûÑÂª∫RockyLinuxËΩØ‰ª∂Ê∫êÈÖçÁΩÆÁöÑÊñáÊú¨Âùó
 *
 * @returns {string} ËøîÂõûRockyLinuxÈÖçÁΩÆÂëΩ‰ª§
 */
function buildRockyBlock() {
    return "# 1. Â∞ÜÊâÄÊúâÁöÑÂÆòÊñπ‰∏ªÈïúÂÉèÂú∞ÂùÄÊõøÊç¢‰∏∫Â±±‰∏úÂ§ßÂ≠¶ÈïúÂÉèÁ´ôÂú∞ÂùÄ„ÄÇ\n" +
        "sed -e 's|^mirrorlist=|#mirrorlist=|g' \\\n" +
        "    -e 's|^#baseurl=http://dl.rockylinux.org/$contentdir|baseurl=https://mirrors.sdu.edu.cn/rocky|g' \\\n" +
        "    -i.bak \\\n" +
        "    /etc/yum.repos.d/Rocky-*.repo\n" +
        "# 2. Êõ¥Êñ∞ÁºìÂ≠ò„ÄÇ\n" +
        "     dnf makecache"
}