import React, { Component } from "react";
import { Layout, Menu, Breadcrumb, Divider, Row, Col, Spin } from "antd";
import { Link, withRouter } from "react-router-dom";
import docMenu from "./menu.json";
import "./HelpPage.css";

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;

/**
 * Â∏ÆÂä©ÊñáÊ°£È°µÈù¢ÁªÑ‰ª∂
 */
export default class DocPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // Â∏ÆÂä©ÊñáÊ°£Ê≠£ÊñáÂÜÖÂÆπ
      docContent: undefined,
      // ÊòØÂê¶Â∑≤ÁªèËé∑Âèñ‰∫ÜÊñáÊ°£
      loaded: false
    };
    this.onMenuItemSelected = this.onMenuItemSelected.bind(this);
  }

  /**
   * Ê†πÊçÆjsonÊñá‰ª∂ÈÄíÂΩíÁîüÊàêÊñáÊ°£ÂØºËà™ËèúÂçïÈ°πÁõÆ
   *
   * @param source ÊñáÊ°£ÂØºËà™ËèúÂçïjsonÊñá‰ª∂
   */
  generateMenuItems(source) {
    source.sort((a, b) => {
      if ("key" in a && "key" in b) {
        return a.key - b.key;
      } else if ("key" in a) {
        return -1;
      } else if ("key" in b) {
        return 1;
      } else {
        return a.title < b.title ? -1 : 1;
      }
    });
    return source.map(menu => {
      if (menu.children) {
        return (
          <SubMenu key={menu.path} title={menu.title}>
            {this.generateMenuItems(menu.children)}
          </SubMenu>
        );
      } else {
        return (
          <Menu.Item key={menu.path}>
            <Link to={menu.path}>{menu.title}</Link>
          </Menu.Item>
        );
      }
    });
  }

  /**
   * Âä®ÊÄÅÂØºÂÖ•ÊñáÊ°£Âπ∂‰øÆÊîπÈ°µÈù¢Ê≠£ÊñáÂÜÖÂÆπ
   *
   * @param path ÊñáÊ°£Ë∑ØÁî±
   */
  importAndExecDoc(path) {
    this.setState({ docContent: undefined });
    import("./doc/js/" + path.split("/")[2] + ".js")
      .then(module => {
        this.setState({
          docContent: module.default(),
          loaded: true
        });
      })
      .catch(() => {
        console.log("import " + path + " failed");
        this.setState({
          docContent: <h1>No Document Here!</h1>,
          loaded: true
        });
      });
  }

  /**
   * ËèúÂçïÈ°πÁõÆË¢´ÈÄâ‰∏≠ÂêéÁöÑÂõûË∞ÉÔºå‰øÆÊîπÂ∏ÆÂä©ÊñáÊ°£ÊòæÁ§∫ÁöÑÊ≠£ÊñáÂÜÖÂÆπ
   *
   * @param info ÈÄâ‰∏≠MenuÈ°πÁöÑ‰ø°ÊÅØ„ÄÇ
   *             ÂÖ∂‰∏≠.keyÊòØË¢´ÈÄâ‰∏≠ÁöÑMenuItemÁöÑkey
   */
  onMenuItemSelected(info) {
    this.setState({
      loaded: false
    });
    this.importAndExecDoc(info.key);
  }

  componentDidMount() {
    this.importAndExecDoc("/doc/docHome");
  }

  render() {
    return (
      <Layout style={{margin: "100px" }}>
        <Sider breakpoint="md" collapsedWidth="0">
          <Menu
            mode="inline"
            defaultOpenKeys={["/doc/docHome"]}
            defaultSelectedKeys={["/doc/docHome"]}
            onSelect={this.onMenuItemSelected}
            style={{ height: "100%" }}
          >
            {this.generateMenuItems(docMenu)}
          </Menu>
        </Sider>
        <Layout
          style={{
            overflowX: "hidden"
          }}
        >
            <Header
              style={{
                padding: "12px 48px",
                background: "white",
                height: "auto"
              }}
            >
              <LinkedBreadcrumb />
              <Divider style={{ marginBottom: "0" }} />
            </Header>
            <Content
            style={{
              padding: "12px 48px 48px",
              background: "white"
              }}
            >
              {this.state.loaded ? (
                this.state.docContent
              ) : (
                <div style={{ textAlign: "center", margin: "50px" }}>
                  <Spin size="large" />
                </div>
              )}
            </Content>
          </Layout>
      </Layout>
    );
  }
}

/**
 * ÂèØÂØºËà™ÁöÑÈù¢ÂåÖÂ±ëÁªÑ‰ª∂
 */
class LinkedBreadcrumb extends React.Component {
  /**
   * Ë∑ØÁî±Ë∑ØÂæÑÂíåÈù¢ÂåÖÂ±ëÊ†áÈ¢òÁöÑÂØπÂ∫îÂÖ≥Á≥ª
   */
  breadcrumbNameMap = {
    "/home": "‰∏ªÈ°µ",
    "/help": "Â∏ÆÂä©ÊñáÊ°£"
  };

  /**
   * Ê†πÊçÆjsonÊñá‰ª∂ÈÄíÂΩíÁîüÊàêÈù¢ÂåÖÂ±ëÂØºËà™È°πÁõÆ
   *
   * @param source ÊñáÊ°£ÂØºËà™ËèúÂçïjsonÊñá‰ª∂
   */
  generateBreadcrumbNameMap(source) {
    for (let index in source) {
      if (source.hasOwnProperty(index)) {
        this.breadcrumbNameMap[source[index].path] = source[index].title;
        if (source[index].children) {
          this.generateBreadcrumbNameMap(source[index].children);
        }
      }
    }
  }

  componentDidMount() {
    this.generateBreadcrumbNameMap(docMenu);
  }

  /**
   * Èù¢ÂåÖÂ±ëÂØºËà™ÁªÑ‰ª∂
   */
  Component = withRouter(props => {
    const { location } = props;
    const pathSnippets = location.pathname.split("/").filter(i => i);
    const extraBreadcrumbItems = pathSnippets.map((_, index) => {
      const url = `/${pathSnippets.slice(0, index + 1).join("/")}`;
      return (
        <Breadcrumb.Item key={url}>
          {this.breadcrumbNameMap[url]}
        </Breadcrumb.Item>
      );
    });
    const breadcrumbItems = [
      <Breadcrumb.Item key="home">
        <Link to="/home">‰∏ªÈ°µ</Link>
      </Breadcrumb.Item>
    ].concat(extraBreadcrumbItems);
    return (
      <Breadcrumb style={{ margin: "20px" }}>{breadcrumbItems}</Breadcrumb>
    );
  });

  render() {
    return <this.Component />;
  }
}